\n            <div id=\"venue-slider\" class=\"splide\">\n                    <div class=\"splide__track\">\n                        <ul class=\"splide__list\">\n                            <li\n                                v-for=\"(item, index) in reviews\"\n                                :key=\"index\"\n                                style=\"\n                                    max-width: 280px;\n                                    width: 100%;\n                                    min-width: 200px;\n                                    margin-right: 12px;\n                                    border: none !important;\n                                \"\n                                class=\"splide__slide\"\n                            >\n                                <div class=\"review-single-view\">\n                                    <div>\n                                        <img :src=\"'/assets/'+item.img\" alt=\"\">\n                                        <div>\n                                            <div class=\"name\">{{item.name}}</div>\n                                            <div class=\"date\">{{item.date}}</div>\n                                        </div>\n                                    </div>\n                                    <div>\n                                        <span class=\"custom-rate\"\n                                            ><i\n                                                class=\"fas fa-star\"\n                                            ></i\n                                        ></span>\n                                        <span class=\"custom-rate\"\n                                            ><i\n                                                class=\"fas fa-star\"\n                                            ></i\n                                        ></span>\n                                        <span class=\"custom-rate\"\n                                            ><i\n                                                class=\"fas fa-star\"\n                                            ></i\n                                        ></span>\n                                        <span class=\"custom-rate\"\n                                            ><i\n                                                class=\"fas fa-star\"\n                                            ></i\n                                        ></span>\n                                        <span class=\"custom-rate\"\n                                            ><i\n                                                class=\"fas fa-star\"\n                                            ></i\n                                        ></span>\n                                    </div>\n                                    <p>{{ item.desc }}</p>\n                                </div>\n                            </li>\n                        </ul>\n                    </div>\n                </div>\n            \n        </div>\n        <section class=\"wed-venue-rating container\">\n            <div class=\"rating-distribution\">\n                <div>\n                    <div>Rating Distribution</div>\n                    <div>\n                        5\n                        <img\n                            src=\"/assets/single-venue-page/Rating Star.svg\"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n                <div>\n                    <div class=\"rating-bar-box\">\n                        <div>\n                            5&nbsp<img\n                                src=\"/assets/single-venue-page/rating-star-small.svg\"\n                                alt=\"\"\n                                style=\"margin-top: -4px\"\n                            />\n                        </div>\n                        <div>\n                            <div class=\"rating-percent\"></div>\n                        </div>\n                        <div>20 ratings</div>\n                    </div>\n                    <div class=\"rating-bar-box\">\n                        <div>\n                            4&nbsp<img\n                                src=\"/assets/single-venue-page/rating-star-small.svg\"\n                                alt=\"\"\n                                style=\"margin-top: -4px\"\n                            />\n                        </div>\n                        <div>\n                            <div class=\"rating-percent\"></div>\n                        </div>\n                        <div>16 ratings</div>\n                    </div>\n                    <div class=\"rating-bar-box\">\n                        <div>\n                            3&nbsp<img\n                                src=\"/assets/single-venue-page/rating-star-small.svg\"\n                                alt=\"\"\n                                style=\"margin-top: -4px\"\n                            />\n                        </div>\n                        <div>\n                            <div class=\"rating-percent\"></div>\n                        </div>\n                        <div>0 ratings</div>\n                    </div>\n                    <div class=\"rating-bar-box\">\n                        <div>\n                            2&nbsp<img\n                                src=\"/assets/single-venue-page/rating-star-small.svg\"\n                                alt=\"\"\n                                style=\"margin-top: -4px\"\n                            />\n                        </div>\n                        <div>\n                            <div class=\"rating-percent\"></div>\n                        </div>\n                        <div>0 ratings</div>\n                    </div>\n                    <div class=\"rating-bar-box\">\n                        <div>\n                            1&nbsp<img\n                                src=\"/assets/single-venue-page/rating-star-small.svg\"\n                                alt=\"\"\n                                style=\"margin-top: -4px\"\n                            />\n                        </div>\n                        <div>\n                            <div class=\"rating-percent\"></div>\n                        </div>\n                        <div>0 ratings</div>\n                    </div>\n                </div>\n            </div>\n            <div class=\"rv-cont\">\n                <div class=\"head\">Rate your experience</div>\n                <form @submit.prevent=\"submitreview($event)\">\n                    <div style=\"display: flex; justify-content: space-between;\">\n                        <div class=\"form-rating\">\n                        <span class=\"custom-rate\"\n                            ><i\n                                @click=\"toggleRating($event)\"\n                                class=\"fas fa-star\"\n                            ></i\n                        ></span>\n                        <span class=\"custom-rate\"\n                            ><i\n                                @click=\"toggleRating($event)\"\n                                class=\"fas fa-star\"\n                            ></i\n                        ></span>\n                        <span class=\"custom-rate\"\n                            ><i\n                                @click=\"toggleRating($event)\"\n                                class=\"fas fa-star\"\n                            ></i\n                        ></span>\n                        <span class=\"custom-rate\"\n                            ><i\n                                @click=\"toggleRating($event)\"\n                                class=\"fas fa-star\"\n                            ></i\n                        ></span>\n                        <span class=\"custom-rate\"\n                            ><i\n                                @click=\"toggleRating($event)\"\n                                class=\"fas fa-star\"\n                            ></i\n                        ></span>\n                        </div>\n                        <button type=\"submit\">submit</button>\n                    </div>\n                    <div class=\"rating-form\">\n                        <label for=\"\">Write a review</label>\n                        <div style=\"position: relative;\">\n                            <textarea\n                            placeholder=\"Type here...\"\n                            maxlength=\"100\"\n                            rows=\"4\"\n                            required\n                            v-model=\"submitReview.description\"\n                            @keydown.enter.prevent\n                        />\n                            <span style=\"position: absolute; top: 15px; right:5px\">1/100</span>\n                        </div>\n                    </div>\n                    <!-- <div v-if=\"logginFirst\">{{ logginFirst }}</div> -->\n                </form>\n            </div>\n        </section>\n    </div>\n</template>\n<script>\nexport default{\n    data() {\n        return{\n            loginFirst:null,\n            submitReview : {\n                description: null,\n            },\n            cont: {},\n            ids: [\n                \"venue-slider\",\n            ],\n            reviews:[\n                {\n                    name: 'Roderick Decker',\n                    date: 'Aug 3rd, 2023',\n                    img: 'e6d6d6ddb585223f158e1048ca0c9485.png',\n                    desc: 'Duis augue in quis eu elit semper. Egestas dolor placerat porta viverra non vitae amet. Ultricies facilisi ut purus in massa. Velit eu eget ullamcorper tincidunt pellentesque tristique nunc justo pharetra. Sit euismod sed nisl netus sed nisl sociis.',\n                },\n                {\n                    name: 'Roderick Decker',\n                    date: 'Aug 3rd, 2023',\n                    img: 'e6d6d6ddb585223f158e1048ca0c9485.png',\n                    desc: 'Duis augue in ue tristique nunc justo pharetra. Sit euismod sed nisl netus sed nisl sociis.',\n                },\n            ],\n        }\n    },\n    methods: {\n        submitreview(e) {\n            return;\n            var _this = this;\n            if (this.logged == \"\") {\n                this.message = \"Log in  as host First\";\n                setTimeout(function () {\n                    _this.message = null;\n                }, 3000);\n                return;\n            }\n            let ownerId = JSON.parse(_this.owner).id;\n            this.logginFirst = null;\n            let user = JSON.parse(this.logged);\n\n            axios\n                .post(`/api/vendor/review`, [\n                    _this.submitReview,\n                    _this.vendorCategory.id,\n                    _this.venueDetails.id,\n                    ownerId,\n                    \"Venue\",\n                    user,\n                ])\n                .then(function (response) {\n                    //_this.getReviews();\n                    _this.reviews.unshift(response.data);\n                    _this.submitReview.title = null;\n                    _this.submitReview.description = null;\n                    _this.submitReview.keywords = null;\n                    _this.message = \"Review Submitted\";\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                    const customRate =\n                        document.querySelectorAll(\".custom-rate > i\");\n                    customRate.forEach(function (item) {\n                        console.log(item);\n                        if (item.classList.contains(\"far\")) {\n                            item.classList.remove(\"far\");\n                            item.classList.add(\"fas\");\n                        }\n                    });\n                    $(\"\");\n                    _this.cont.destroy();\n\n                    var element = _this.$refs[\"reviewCont\"];\n                    setTimeout(function () {\n                        element.scrollIntoView(true);\n                        _this.cont.mount();\n                    }, 100);\n                })\n                .catch((error) => {\n                    console.log(error.response.data.errors);\n                });\n        },\n        toggleRating(e) {\n            const customRate = document.querySelectorAll(\".wed-venue-rating .custom-rate > i\");\n            //console.log(indexOf(item));\n            let stars = e.currentTarget;\n            var clicked = Array.from(customRate).indexOf(stars);\n            // console.log(clicked)\n            if (stars.classList.contains(\"fas\")) {\n                for (let i = clicked + 1; i <= 4; i++) {\n                    customRate.item(i).classList.remove(\"fas\");\n                    customRate.item(i).classList.add(\"far\");\n                }\n            } else {\n                for (let j = 0; j <= clicked; j++) {\n                    customRate.item(j).classList.remove(\"far\");\n                    customRate.item(j).classList.add(\"fas\");\n                }\n            }\n            this.submitReview.rating = clicked + 1;\n            // console.log(rate.item(clicked));\n        },\n        setSplider() {\n            let _this = this;\n            this.$nextTick(function () {\n                this.ids.forEach(function (item, index) {\n                    _this.cont[item] = new Splide(`#${item}`, {\n                        gap: 5,\n                        rewind: true,\n                        pagination: false,\n                        cover: true,\n                        isNavigation: true,\n                        arrows: true,\n                        breakpoints: {\n                            567: {\n                                arrows: false,\n                                wheel: true,\n                                direction: \"ltr\",\n                                releaseWheel: false,\n                            },\n                        },\n                    });\n                    _this.cont[item].mount();\n                });\n            });\n        },\n    }\n}\n\n</script>\n<style scoped>\n.wed-venue-rating {\n    margin: auto;\n    display: grid;\n    grid-template-columns: minmax(300px, 500px) minmax(350px, 700px);\n    grid-column-gap: 7em;\n    justify-content: space-between;\n    margin-top: 30px;\n}\n\n.rating-distribution > div {\n    display: flex;\n    justify-content: space-between;\n}\n\n.rating-distribution > div:nth-child(1) > div:nth-child(1) {\n    color: #000;\nfont-family: Poppins;\nfont-size: 18px;\nfont-style: normal;\nfont-weight: 600;\nline-height: normal;\n}\n.rating-distribution > div:nth-child(1) > div:nth-child(2) {\n    background: #2fa706;\n    border-radius: 2.10938px;\n    color: #EBEBEB;\nfont-family: Roboto;\nfont-size: 18.683px;\nfont-style: normal;\nfont-weight: 500;\nline-height: normal;\n    padding: 5px 7px;\n}\n\n.rating-distribution > div:nth-child(2) {\n    margin-top: 1.5em;\n    display: flex;\n    flex-direction: column;\n}\n.rating-bar-box {\n    display: flex;\n    margin-bottom: 20px;\n}\n.rating-bar-box > div:nth-child(1) {\n    font-family: Roboto;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 18.683px;\n    line-height: 22px;\n    color: #868686;\n    align-self: center;\n    width: 2em;\n}\n\n.rating-bar-box > div:nth-child(2) {\n    border-radius: 100px;\n    background: linear-gradient(0deg, #EBEBEB 0%, #EBEBEB 100%), linear-gradient(0deg, #EBEBEB 0%, #EBEBEB 100%), #EBEBEB;\n    margin: auto 1em;\n    height: 13px;\n    width: 75%;\n}\n.rating-bar-box > div:nth-child(3) {\n    font-family: Roboto;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 11.412px;\n    line-height: 13px;\n    color: #868686;\n    align-self: center;\n    width: 5em;\n}\n.rating-percent {\n    width: 70%;\n    background-color: #2fa706;\n    border-radius: 100px;\n    height: 100%;\n}\n.form-rating {\n    position: relative;\n}\n.rate {\n    display: none;\n}\n.custom-rate {\n    color: #fed21e;\n    font-size: 30px;\n}\n.rating-form {\n    display: flex;\n    flex-direction: column;\n    position: relative;\n}\n.rating-form label {\n    color: #000;\nfont-family: Roboto;\nfont-size: 16px;\nfont-style: normal;\nfont-weight: 500;\nline-height: normal;\n    padding: 0.7em 0;\n}\n.rating-form textarea {\n    outline: none;\n    border-radius: 6px;\n    border: 1.5px solid #CBCBCB;\n    padding: 15px;\n    padding-right: 4em;\n    width: 100%;\n    background-color: #ffffff !important;\n}\n.rating-form textarea::placeholder {\n    font-family: Roboto;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12.4666px;\n    line-height: 15px;\n    color: #b5b5b5;\n\n}\n.rating-form > span {\n    position: absolute;\n    bottom: 0;\n    right: 0;\n    padding: 0 10px 5px 0;\n    font-family: Roboto;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12.4666px;\n    line-height: 15px;\n    color: #b5b5b5;\n}\n\n.wed-venue-rating form  button {\n    color: #FFF;\n    text-align: center;\n    font-family: Inter;\n    font-size: 14px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    border-radius: 5px;\n    border: 1px solid #C4456F;\n    background: #C4456F;\n    padding: 15px;\n}\n.head{\n    color: #000;\n    font-family: Poppins;\n    font-size: 18px;\n    font-style: normal;\n    font-weight: 600;\n    line-height: normal;\n}\n.review-single-view {\n    border-radius: 7.096px;\n    border: 4.435px solid #E7E7E7;\n    background: #FFF;\n    max-width: 350px;\n    min-width: 280px;\n    padding:23px;\n    height: 100%;\n}\n.review-single-view > div:nth-child(1){\n    display: flex;\n}\n.review-single-view img {\n    border-radius: 58.545px;\n    width: 58.545px;\n    height: 58.545px;\n    margin-right: 15px;\n}\n.review-single-view .name{\n    color: #000;\n    text-align: center;\n    font-family: Inter;\n    font-size: 15.967px;\n    font-style: normal;\n    font-weight: 600;\n    line-height: normal;\n    text-transform: capitalize;\n    margin-bottom: 10px;\n}\n.review-single-view .date{\n    color: #878787;\n    font-family: Inter;\n    font-size: 12.419px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    text-transform: capitalize;\n}\n.review-single-view p{\n    color: #000;\n    font-family: Inter;\n    font-size: 14.193px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: 133.023%; /* 18.88px */\n    text-transform: capitalize;\n}\n\n@media screen and (max-width: 840px) {\n    .wed-venue-rating{\n        flex-wrap: wrap;\n        display:flex;\n    }\n    .rating-distribution{\n        width: 100%;\n    }\n    .rv-cont {\n        width: 100%;\n    }\n}\n\n</style>"],"sourceRoot":""}]);
